.page-bottom_banner {
  padding: 55px 80px;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
}

.page-bottom_banner:before {
  display: block;
  content: '';
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, rgba(19, 58, 88, 0.8) 0.07%, rgba(32, 104, 132, 0) 99.99%);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
}

.page-bottom_banner:hover img {
  transform: scale(1.1);
}

.page-bottom_banner img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  transition: .3s all ease;
}

.page-bottom_banner h4 {
  color: #fff;
  font-size: 36px;
  line-height: 50px;
  margin-bottom: 8px;
  position: relative;
  z-index: 10;
}

.page-bottom_banner p {
  font-size: 17px;
  line-height: 26px;
  max-width: 500px;
  margin-bottom: 25px;
  position: relative;
  z-index: 10;
  color: #fff;
}

.page-bottom_banner a {
  display: inline-block;
  width: auto;
  background-color: #23A8CD;
  box-shadow: 0 8px 24px 0 rgba(24, 120, 147, 0.24);
  padding: 15px 25px;
  text-align: center;
  color: #fff;
  font-size: 15px;
  font-weight: 500;
  line-height: 20px;
  border-radius: 12px;
  position: relative;
  z-index: 10;
  transition: .3s all ease;
}

.page-bottom_banner a:hover {
  color: #fff !important;
  background-color: #1c84a1;
}

@media screen and (max-width: 500px) {
  .page-bottom_banner {
    padding: 30px 20px;
  }
  .page-bottom_banner h4 {
    font-size: 26px;
    line-height: 36px;
  }
  .page-bottom_banner p {
    font-size: 16px;
    line-height: 23px;
    margin-bottom: 40px;
  }
  .page-bottom_banner a {
    font-size: 15px;
    line-height: 20px;
    padding: 12px 20px;
  }
}

